body {
  background: #1e555c;
  color: white;
  font-size: 30px;
  text-align: center;

}
.main-title {
  font-size: 50px;
  margin-bottom: 20px;
}
.length-control {
  width: 250px;
}
button {
  background: none;
  outline: none;
  border: none;
  color: white;
  cursor: pointer;
}
#container {
  display: flex;
  height: 100vh;
  justify-content: center;
  align-items: center;



}
.btn-level,
.length-control {
  display: inline-block;

}
.btn-level:nth-child(3) {
  width: 40px;
}
.timer {
  border: 7px solid #13353a;
  margin: 20px auto 10px auto;
  width: 270px;
  height: 160px;
  border-radius: 50px;
  position: relative;
 

}
.timer-wrapper {
  position: absolute;
  width: 190px;
  height: 110px;
  left: 50%;
  top: 50%;
  margin-left: -95px;
  
}

#time-left {
  font-family: digital;
  font-size: 80px;
}
.timer-control button:active {
  color: #13353a;
  
}
.timer-control{
    font-size: 10px;
    margin-left: -10px;
   
}
#break-label{
  margin: 30px;
}
#break-increment{
    margin-right: 100px;
}
#break-decrement{
  margin-left: 25px;
}
#session-decrement{
  margin-left: 20px;
}
#start_stop{
  font-size: 50px;
  height: 50px;
  font-size: 46px;

}

#stop{
  width: 50px;
  height: 50px;
  font-size: 50px;
  margin-left: -2px;
}
#reset{
  width: 50px;
  height: 50px;
  margin-left: 5px;
  font-size: 50px;
}